import { ValueOf } from '@utils/helper';

export const IconSize = {
  Large: 'lg',
  Medium: 'md',
  Small: 'sm',
  Extrasmall: 'xs',
} as const;

export type IconSizeTypes = ValueOf<typeof IconSize>;

export enum Icons {
  Add = 'add',
  InputClearValue = 'close',
  DropDown = 'chevron-down',
  Minimize = 'minimize',
  PasswordHide = 'eye-close',
  PasswordView = 'eye',
  Search = 'search',
  Info = 'info',
}

export enum IconType {
  Button = 'button',
  ButtonComp = 'buttonComp',
  ButtonExpandable = 'buttonExpandable',
  ButtonLink = 'buttonLink',
  ButtonIconOnly = 'iconOnly',
  Link = 'link',
  Feedback = 'feedback',
  Input = 'input',
  Label = 'label',
  Message = 'message',
  Combobox = 'combobox',
  Chipchoice = 'chipchoice',
  SegmentedControlItem = 'segmentedControlItem',
  SegmentedControlItemIconOnly = 'segmentedControlItemIconOnly',
}

export function transformIconSize(type: string, size: string): IconSizeTypes {
  const map = {
    link: { sm: 'xs', md: 'sm', lg: 'sm' },
    button: { xs: 'xs', sm: 'sm', md: 'sm', lg: 'sm' },
    buttonComp: { xs: 'xs', sm: 'md', md: 'lg' },
    buttonExpandable: { sm: 'xs', md: 'sm', lg: 'sm' },
    buttonLink: { sm: 'xs', md: 'sm', lg: 'sm' },
    iconOnly: { xs: 'xs', sm: 'sm', md: 'md', lg: 'md' },
    feedback: { sm: 'sm', md: 'md', lg: 'lg' },
    input: { sm: 'xs', md: 'xs', lg: 'sm' },
    label: { sm: 'xs', md: 'xs', lg: 'sm' },
    message: { sm: 'xs', md: 'xs', lg: 'md' },
    combobox: { sm: 'xs', md: 'xs', lg: 'md' },
    chipchoice: { sm: 'xs', md: 'sm' },
    segmentedControlItem: { sm: 'xs', md: 'sm', lg: 'sm', xl: 'sm' },
    segmentedControlItemIconOnly: { sm: 'xs', md: 'sm', lg: 'md', xl: 'md' },
  };
  return map[type][size];
}
